.Home{
  height: fit-content;
  background-image: url("../img/stick-it.png");
  background-color: white;
}

.wrapper{
  text-align: left;
  width: 100%;
  height: 100%;
  max-width: 1080px;
  margin: 100px auto;
  align-items: center;
}

.divider{
  color: #fff;
  mix-blend-mode: difference;
}

.grid-container{
  height: 100vh;
  display: flex;
  justify-content: center;
}


.grid-item{
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 100px;
}

.text-category{
font-style: normal;
font-weight: bold;
font-size: 18px;
line-height: 21px;
min-width: 260px;
margin-bottom: 25px;
color: #fff;
mix-blend-mode: difference;
}

.text-main{
font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 150%;
color: #fff;
mix-blend-mode: difference;
}



.link{
font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 150%;
color: #fff;
mix-blend-mode: difference;
}

.a_diff{
  color: #fff;
  mix-blend-mode: difference;
}

