.nav {
    position: fixed;
    width: 100%;
    max-width: 1080px;
    top: 0px;
    right:5px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    padding: 10px 20px;
    mix-blend-mode: difference;
    color: #fff;
  }
 
  .nav a {
    text-decoration: none;
    font-size: 16px;
    text-align: center;
    font-weight: 600;
    margin: 8px;
  }
 
  .nav a:not(:last-child) {
